import common from '../../../common/common.js'

export default {
	data() {
		return {
			userid:'',
			shopInfo:[],
			shop_lat:'',
			shop_lng:'',
		};
	},
	onLoad() {
		var that = this
		var shopInfo = uni.getStorageSync('shopInfo');
		that.shopInfo = shopInfo
		
	},
	methods:{
		//打开地图
		openmap(){
			var that = this
			uni.chooseLocation({
				success: function (res) {
					//that.title = res.name
					that.shopInfo.shop_address = res.address
					that.shop_lat = res.latitude
					that.shop_lng = res.longitude
				}
			});
		},
		//提交
		formSubmit(e){
			var that = this
			var data = e.detail.value;
			
			
			
			if(data.shop_title == ''){					
				uni.showToast({
					title: '请输入商户名称',
					duration: 1500,
					icon :'none'
				});
				return false
			}
			if(data.shop_address == ''){
				uni.showToast({
					title: '请输入商户地址',
					duration: 1500,
					icon :'none'
				});
				return false
			}
			if(data.shop_mobile == ''){
				uni.showToast({
					title: '请输入联系电话',
					duration: 1500,
					icon :'none'
				});
				return false
			}
			if(data.shop_worktime == ''){
				uni.showToast({
					title: '请输入营业时间',
					duration: 1500,
					icon :'none'
				});
				return false
			}
			if(data.shop_zhuying == ''){
				uni.showToast({
					title: '请输入主营业务',
					duration: 1500,
					icon :'none'
				});
				return false
			}
			
			uni.showLoading({
				title: '处理中'
			});
			
			uni.request({
				url: common.sjApiUrl + 'merchant/updateShopInfo',
				method: 'GET',
				data: {
					shop_id:that.shopInfo.shop_id,
					shop_title:data.shop_title,
					shop_address:data.shop_address,
					shop_mobile:data.shop_mobile,
					shop_worktime:data.shop_worktime,
					shop_zhuying:data.shop_zhuying,
					shop_lat:that.shop_lat,
					shop_lng:that.shop_lng
				},
				success: res => {
					uni.hideLoading();
					
					var shopInfo = res.data.userInfo;
					
					uni.setStorageSync('shopInfo',shopInfo);
					that.shopInfo = shopInfo
					
					uni.showToast({
						title: '操作成功',
						duration: 1500,
						icon:'success'
					});
					uni.navigateBack();
				},
				fail: () => {},
				complete: () => {}
			});
			
			
			
		}
	}
}
